:root {
  /******************* Colors ********************/
  --black: hsl(0, 0%, 0%);
  --tanL: hsl(54, 100%, 90%);
  --tanD: hsl(54, 50%, 75%);
  --white: hsl(0, 0%, 100%);
  --white50: hsla(0, 0%, 100%, .5);
  --white75: hsla(0, 0%, 100%, .75);
  --darkGrey1: hsl(0, 0%, 20%);
  --darkGrey2: hsl(72, 0%, 24%);
  --darkturquoise: hsl(187, 49%, 23%);
  --navy: hsl(240, 100%, 25%);
  --midnightBlue: hsl(240, 64%, 27%);
  --red: hsl(0, 100%, 50%);
  --maroon: hsl(0, 100%, 20%);
  --lightMaroon: hsl(0, 100%, 30%);
  --green: hsl(120, 100%, 30%);
  --forrestGreen: hsl(120, 100%, 20%);
  --themeBGColorMain: var(--darkGrey1);
  --themeBGColorAccent: var(--darkturquoise);
  --Verdana: verdana, geneva, arial, helvetica, sans-serif;
  --TNR: "Times New Roman", times, serif;
  --Courier: "Courier New", courier, monospace;
  --Helvetica: "Helvetica Neue", helvetica, sans-serif;
  --MSSans: "MS Sans Serif", geneva, sans-serif;
  --SegoeUI: "Segoe UI", arial, helvetica, sans-serif;
  --Tahoma: tahoma, verdana, segoe, sans-serif;
  --Arial: arial, helvetica, san-serif;
  --Roboto:
    "Roboto",
    -apple-system,
    blinkmacsystemfont,
    "Segoe UI",
    roboto,
    "Helvetica Neue",
    arial,
    sans-serif,
    "Apple Color Emoji",
    "Segoe UI Emoji",
    "Segoe UI Symbol";
  --h1: 3rem;
  --h2: 2.25rem;
  --h3: 1.5rem;
  --h4: 1.25rem;
  --h5: 1.1rem;
  --size-sm: 576px;
  --size-md: 768px;
  --size-lg: 992px;
  --size-xl: 1200px;
  --small-text: .9rem;
}
:root .themeColorAccent {
  color: var(--themeBGColorAccent);
}

.accordion input {
  display: none;
}
.accordion label {
  color: var(--white);
  background-color: var(--themeBGColorMain);
  display: block;
  padding: 8px 22px;
  margin: 0 0 1px 0;
  border-radius: 3px;
}
.accordion label::before {
  font-family: ForkAwesome, serif;
  margin-right: 0.5rem;
  content: "";
  font-weight: 900;
}
.accordion input:checked + label::before {
  font-family: ForkAwesome, serif;
  margin-right: 0.5rem;
  content: "";
  font-weight: 900;
}
.accordion label:hover {
  background: #4e8774;
}
.accordion .content {
  background: #fff;
  padding: 10px 25px;
  border: 1px solid #a7a7a7;
  margin: 0 0 1px 0;
  border-radius: 3px;
}
.accordion input + label + .content {
  opacity: 0;
  height: 0;
  font-size: 0;
  transition: linear 0.8s;
  padding: 0 25px;
}
.accordion input:checked + label + .content {
  opacity: 1;
  font-size: 1rem;
  padding: 8px 25px;
  height: auto;
  min-height: auto;
}

.sidebar .accordion input,
.fullpage .accordion input {
  display: none;
}
.sidebar .accordion label,
.fullpage .accordion label {
  color: var(--white);
  background-color: var(--themeBGColorMain);
  display: block;
  padding: 8px 22px;
  margin: 0;
  border-radius: 0.75rem 0.75rem 0 0;
  font-weight: 900;
  font-size: var(--h5);
}
.sidebar .accordion label::before,
.fullpage .accordion label::before {
  margin-right: 0;
  content: none;
}
.sidebar .accordion input:checked + label::before,
.fullpage .accordion input:checked + label::before {
  content: none;
  margin-right: 0;
}
.sidebar .accordion label:hover,
.fullpage .accordion label:hover {
  background: #4e8774;
}
.sidebar .accordion .content,
.fullpage .accordion .content {
  border-radius: 0 0 0.75rem;
  margin-bottom: 1.5rem;
  padding: 1rem;
}
.sidebar .accordion .content a,
.sidebar .accordion .content span,
.fullpage .accordion .content a,
.fullpage .accordion .content span {
  display: block;
}
.sidebar .accordion input + label + .content,
.fullpage .accordion input + label + .content {
  opacity: 1;
  font-size: 1rem;
  transition: linear 0.8s;
  height: auto;
}
.sidebar .accordion input:checked + label + .content,
.fullpage .accordion input:checked + label + .content {
  opacity: 0;
  height: 0;
  font-size: 0;
  padding: 8px 25px;
  min-height: auto;
}

@media all and (max-width: 768px) {
  .sidebar .accordion input + label::before {
    font-family: ForkAwesome, serif;
    margin-right: 0.5rem;
    content: "";
    font-weight: 900;
  }
  .sidebar .accordion input:checked + label::before {
    font-family: ForkAwesome, serif;
    margin-right: 0.5rem;
    content: "";
    font-weight: 900;
  }
  .sidebar .accordion label:hover {
    background: #4e8774;
  }
  .sidebar .accordion .content a,
.sidebar .accordion .content span {
    display: inline-block;
    margin-right: 3rem;
  }
  .sidebar .accordion input + label + .content {
    opacity: 0;
    font-size: 0;
    padding: 8px 25px;
    height: auto;
    min-height: auto;
  }
  .sidebar .accordion input:checked + label + .content {
    opacity: 1;
    font-size: 16px;
    transition: linear 0.5s;
    padding: 0 25px;
    height: auto;
  }
}
/************** Carousel BS way  *************/
.carousel {
  margin: 0 auto;
  width: 80%;
  position: relative;
}
.carousel .carousel__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.carousel .carousel__button--left, .carousel .carousel__button--right {
  position: absolute;
}
.carousel .carousel__button--left {
  left: 0;
}
.carousel .carousel__button--right {
  right: 0;
}
.carousel .carousel__button img {
  height: 20px;
  width: 20px;
}
.carousel .carousel__image {
  max-width: 100%;
  height: auto;
  max-height: 400px;
}
.carousel .carousel__nav {
  display: flex;
  justify-content: center;
}
.carousel .carousel__nav .carousel__indicator {
  border: 0;
  border-radius: 50%;
  height: 15px;
  width: 15px;
  background-color: var(--themeBGColorAccent);
  margin: 0 10px;
}
.carousel .carousel__slide {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}
.carousel .carousel__track {
  list-style: none;
  margin: 0;
  padding: 0;
}
.carousel .carousel__track-container {
  background-color: lightblue;
  padding: 10px;
  height: 100%;
}

.frontpage-title {
  margin: 1rem auto;
  font-weight: 900;
  font-size: var(--h3);
}

.frontpage-section {
  display: flex;
  flex-flow: column wrap;
}
.frontpage-section p {
  max-width: 72ch;
}
.frontpage-section .git-section,
.frontpage-section .site-section {
  display: inline-flex;
  justify-content: space-around;
}
@media all and (max-width: 768px) {
  .frontpage-section .git-section,
.frontpage-section .site-section {
    flex-wrap: wrap;
  }
}
.frontpage-section .git-section a,
.frontpage-section .site-section a {
  margin-bottom: 0;
}
.frontpage-section .git-section a:hover,
.frontpage-section .site-section a:hover {
  outline: 2px solid var(--themeBGColorAccent);
  color: inherit;
  text-decoration: none;
}
.frontpage-section .git-section .card,
.frontpage-section .site-section .card {
  box-shadow: none;
  max-height: 500px;
}
.frontpage-section .git-section .card .card__header,
.frontpage-section .site-section .card .card__header {
  background-color: inherit;
  color: var(--themeBGColorAccent);
  font-size: 1.6rem;
}
.frontpage-section .git-section .card .card__header a,
.frontpage-section .site-section .card .card__header a {
  background-color: inherit;
  color: inherit;
}
.frontpage-section .git-section .card .card__body,
.frontpage-section .site-section .card .card__body {
  font-weight: 500;
}
.frontpage-section .git-section .card .card__body .img-fluid,
.frontpage-section .site-section .card .card__body .img-fluid {
  height: auto;
  max-width: 80%;
}
.frontpage-section .git-section > * {
  flex-basis: 1;
}
.frontpage-section .git-section .card--bordered {
  margin: 1rem 2rem;
}
.frontpage-section .git-section .card--bordered .card__header {
  text-align: center;
}
.frontpage-section .git-section .git-icons {
  color: var(--themeBGColorAccent);
  display: flex;
  justify-content: space-around;
  margin-bottom: 0.5rem;
}
.frontpage-section .git-section .git-icons a {
  margin: auto 1rem;
}
@media all and (max-width: 992px) {
  .frontpage-section .site-section figure {
    display: none;
  }
}
.frontpage-section .site-section figure > .figimg {
  padding-bottom: 0.5rem;
  display: inline-block;
}
.frontpage-section .site-section figure > figcaption {
  font-size: var(--small-text);
  padding-left: 2rem;
}
@media all and (max-width: 992px) {
  .frontpage-section .site-section > div {
    margin: 0 0.75rem;
  }
}
.frontpage-section .site-section .h3,
.frontpage-section .site-section a .h3 {
  display: block;
  margin: 1rem 0;
  font-weight: 900;
  font-size: 1.7rem;
}
.frontpage-section .site-section img {
  margin: 0 auto;
  width: 600px;
  padding: 2rem;
}
@media all and (max-width: 762px) {
  .frontpage-section .site-section img {
    max-width: 80%;
  }
}
.frontpage-section .site-section .card--bordered {
  margin: 0 auto;
  width: 100%;
}
.frontpage-section .site-section .card--bordered .card__body p {
  margin: 10px 0;
}

